.lead-button {
  width: auto;
  font-weight: 700;
  font-size: 1.3em;
  font-family: "sofia-pro-soft";
  border-radius: 4px;
  color: #FFFFFF;
  outline: none;
  appearance: none;
  position: relative;
  -webkit-appearance: none;
  -moz-appearance: none;
  text-shadow: none;
  border: none;
  box-shadow: none;
  display: flex;
  justify-content: center; }
  .lead-button:before {
    content: '';
    position: absolute;
    right: 1em;
    top: 50%;
    margin-top: -9px;
    width: 20px;
    height: 20px;
    border: 4px solid;
    border-left-color: transparent;
    border-radius: 50%;
    opacity: 0;
    transition-duration: .5s;
    transition-property: opacity;
    animation-duration: 0.4s;
    animation-iteration-count: infinite;
    animation-name: rotate;
    animation-timing-function: linear; }
  .lead-button:after {
    content: '';
    display: inline-block;
    height: 100%;
    width: 0px;
    transition-delay: .2s;
    transition-duration: .3s;
    transition-property: width; }
  .lead-button.loading {
    pointer-events: none;
    cursor: not-allowed; }
    .lead-button.loading:not(.expand) {
      text-align: left; }
    .lead-button.loading:before {
      transition-delay: .5s;
      transition-duration: 0.4s;
      opacity: 1; }
    .lead-button.loading:after {
      transition-delay: 0s;
      width: 20px; }
  .lead-button.image-button {
    height: 30px;
    width: 30px;
    justify-content: center;
    align-items: center;
    display: flex; }
    .lead-button.image-button svg {
      height: 15px;
      width: 15px; }
  .lead-button.fixed-button-right {
    position: absolute;
    top: 0;
    right: 1em; }
  .lead-button.fixed-button-left {
    position: absolute;
    top: 0;
    left: 1em; }
  .lead-button.primary {
    background-color: #50E3C2; }
    .lead-button.primary:hover {
      background-color: #45e1be;
      cursor: pointer; }
  .lead-button.medium {
    height: 2.5em;
